import React, { Component } from 'react';
import NavBar from './styles/Nav.styles';
import PropTypes from 'prop-types';
import Badge from './styles/UserBadge.styles';
import Backdrop from './Loader';

class Nav extends Component {
  state = {
    open: false
  };

  render() {
    const { user, page, logout, changePage } = this.props;
    const { open } = this.state;
    const menuAnim = open
      ? 'rotateY(90deg) translateY(100%) opacity(0);'
      : 'rotateY(0) translateY(0) opacity(1);';
    return (
      <NavBar>
        <div>
          <h1>recordStore</h1>
          <h2>what are you listening to?</h2>
        </div>
        <ul>
          {user ? (
            <>
              <li
                menuAnim={menuAnim}
                delay={'0'}
                onClick={() => logout()}
                current={page === 'splash' ? '1' : '0'}>
                Sign Out
              </li>
              <li
                menuAnim={menuAnim}
                delay={'.2s'}
                onClick={() => changePage('account')}
                current={page === 'account' ? '1' : '0'}>
                Account
              </li>
              <li
                menuAnim={menuAnim}
                delay={'.4s'}
                onClick={() => changePage('store')}
                current={page === 'store' ? '1' : '0'}>
                My Store
              </li>
              <li
                menuAnim={menuAnim}
                delay={'.6s'}
                onClick={() => changePage('add')}
                current={page === 'add' ? '1' : '0'}>
                +
              </li>
            </>
          ) : (
            <li
              onClick={() => changePage('splash')}
              current={page === 'splash' ? '1' : '0'}>
              Sign In
            </li>
          )}
        </ul>
        {user && (
          <Badge
            src={user.picture}
            alt={user.name.first}
            margin="right"
            onClick={() => this.setState({ open: !open })}
          />
        )}
        {open && <Backdrop onClick={() => console.log('hello?')} />}
      </NavBar>
    );
  }
}

Nav.propTypes = {
  user: PropTypes.object,
  changePage: PropTypes.func.isRequired,
  logout: PropTypes.func.isRequired,
  page: PropTypes.string.isRequired
};

export default Nav;
